/**
 *@date: 2018-10-11 14:25:00
 *@author: jing.fu@gosafenet.com
 *@desc: 地铁线路规划结果类
 */
export default class DirectionPanel {
// eslint-disable-next-line no-useless-constructor
  constructor ({start, end, totalTimes, totalStops, lines, curSLines}) {
    this.start = start
    this.end = end
    this.totalTimes = totalTimes
    this.totalStops = totalStops
    this.lines = lines
    this.curSLines = curSLines
  }
}



