<template>
  <section>
    <b-navbar :showLeft="true" title="城市地铁图"></b-navbar>

    <yd-search v-model="queryKey" :on-submit="initBMapSub" :on-cancel="handeleCancel"
               placeholder="输入城市搜索地铁图"></yd-search>

    <section id="Subway-container"
             :style="{width:'100%',height:screenHeight+'px'}"></section>

    <yd-actionsheet :items="actionsheets" v-model="showActionsheet" cancel="取消"></yd-actionsheet>

    <yd-popup v-model="showDirection" position="center" width="90%">
      <section class="popup-wrapper-container">
        <section class="popup-title">
          <section>线路规划</section>
          <section @click="showDirection = false">
            <yd-icon name="error-outline" size=".3rem" color="#999999"></yd-icon>
          </section>
        </section>
        <section class="popup-content">
          <section class="content-form-container">
            <section class="form-container">
              <yd-cell-group>
                <yd-cell-item style="height: .8rem;">
                  <yd-input slot="right" v-model="direction.start" placeholder="起点"></yd-input>
                </yd-cell-item>
                <yd-cell-item style="height: .8rem;">
                  <yd-input slot="right" v-model="direction.end" placeholder="终点"></yd-input>
                </yd-cell-item>
              </yd-cell-group>
            </section>
            <section class="icon-container">
              <yd-icon @click.native="handleChangedirection" name="icon-jiaohuan" size=".6rem" color="#4A5B79"
                       custom></yd-icon>
            </section>
          </section>
        </section>
        <section class="popup-footer">
          <yd-button @click.native="handleSearchdirection" size="large" shape="circle" bgcolor="#4A5B79" color="#fff">搜&nbsp;索</yd-button>
        </section>
      </section>
    </yd-popup>
  </section>
</template>

<script type="text/ecmascript-6">
  import BMapSub from 'BMapSub'
  import BMap from 'BMap'
  import BNavbar from '@/components/basis/BNavbar'
  import DirectionPanel from '@/common/js/direction'
  import {mapMutations} from 'vuex'

  export default {
    name: 'Index',
    components: {
      BNavbar
    },
    methods: {
      ...mapMutations({
        handleSetDirectionPanel: 'Set_DirectionPanel'
      }),
      geolocation () {
        let _that = this
        let geolocation = new BMap.Geolocation()

        geolocation.getCurrentPosition(function (ret) {
          if (this.getStatus() === BMAP_STATUS_SUCCESS) {
            _that.initBMapSub(ret.address.city)

          } else {
          }
        }, {enableHighAccuracy: true, maximumAge: 0})
      },
      initBMapSub (city) {
        let _that = this

        let subwayCityName = city || _that.queryKey
        let list = BMapSub.SubwayCitiesList

        let subwaycity = null

        for (let i = 0; i < list.length; i++) {
          if (subwayCityName.indexOf(list[i].name) > -1) {
            subwaycity = list[i]
            break
          }
        }

        if (subwaycity != null) {
          let subway = new BMapSub.Subway(document.getElementById('Subway-container'), subwaycity.citycode)

          let zoomControl = new BMapSub.ZoomControl({
            anchor: BMAPSUB_ANCHOR_BOTTOM_RIGHT,
            offset: new BMapSub.Size(10, 80)
          })
          subway.addControl(zoomControl)
          subway.setZoom(0.3)

          _that.subway = subway

          subway.addEventListener('tap', function (e) {
            _that.tapStationname = e.station.name
            _that.showActionsheet = true
          })
        } else {

          _that.$dialog.toast({
            mes: '抱歉，没有找到相应地铁图',
            timeout: 1500,
            callback: () => {
              _that.handeleCancel()
            }
          })
        }
      },
      handeleCancel () {
        this.queryKey = ''
      },
      handleChangedirection () {
        let _that = this
        let temp = _that.direction.start

        _that.direction.start = _that.direction.end
        _that.direction.end = temp
      },
      handleSearchdirection () {
        let _that = this

        if (_that.direction.start === '' || _that.direction.start.length <= 0) {
          _that.$dialog.notify({
            mes: '请填写路线规划起点',
            timeout: 1800
          })
          return false

        } else if (_that.direction.end === '' || _that.direction.end.length <= 0) {
          _that.$dialog.notify({
            mes: '请填写路线规划终点',
            timeout: 1800
          })
          return false

        }
        let drct = new BMapSub.Direction(_that.subway)

        drct.search(_that.direction.start, _that.direction.end)

        _that.subway.addEventListener('directioncomplete', function (ret) {
          let retInfo = ret.currentTarget.directionPanel.info
          let result = retInfo.direction.json.result

          let panel = new DirectionPanel({
            start: result.s_query,
            end: result.e_query,
            totalTimes: retInfo.totalTimes,
            totalStops: retInfo.totalStops,
            lines: retInfo.lines,
            curSLines: retInfo.curSLines
          })

          _that.handleSetDirectionPanel(panel)

          _that.$router.push({path: '/subway/ret'})
        })

        _that.showDirection = false
        _that.direction = {
          start: '',
          end: ''
        }
      }
    },
    data () {
      return {
        showDirection: false,
        direction: {
          start: '',
          end: ''
        },
        tapStationname: '',
        showActionsheet: false,
        actionsheets: [
          {
            label: '站点详情',
            callback: () => {
              let options = {
                detailRenderOptions: {
                  autoRender: true
                },
                onSearchSuccess: function (ret) {
                }
              }

              let detail = new BMapSub.DetailInfo(this.subway, options)

              detail.search(this.tapStationname)
            }
          },
          {
            label: '路线规划',
            callback: () => {
              this.direction.start = this.tapStationname
              this.showDirection = true
            }
          }
        ],
        queryKey: '',
        screenHeight: window.screen.height - 100
      }
    },
    beforeRouteLeave (to, from, next) {
      let _that = this

      if (to.path !== '/subway/ret') {
        _that.$destroy()
      }

      next()
    },
    created () {
      this.$nextTick(() => {
        this.geolocation()
      })
    }
  }
</script>

<style type="text/css" scoped>

  .popup-wrapper-container >>> .popup-footer .yd-btn-block {
    height: .7rem;
    line-height: .7rem;
    margin-top: .1rem;
  }

  .popup-wrapper-container >>> .content-form-container .form-container .yd-cell-item:not(:last-child):after {
    margin-left: 0rem;
  }

  .popup-wrapper-container >>> .content-form-container .form-container .yd-cell-right {
    min-height: .8rem;
  }

  .popup-wrapper-container >>> .content-form-container .form-container .yd-cell-item {
    padding-left: 0rem;
  }

  .popup-wrapper-container >>> .content-form-container .form-container input {
    height: .8rem;
  }

  .popup-wrapper-container >>> .content-form-container .form-container {
    width: 80%;
  }

  .popup-wrapper-container >>> .content-form-container .icon-container {
    width: 20%;
    display: inline-flex;
    display: -webkit-inline-flex;
    justify-content: center;
    -webkit-justify-content: center;
  }

  .popup-wrapper-container >>> .content-form-container {
    width: 100%;
    display: flex;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    align-items: center;
  }

  .search-container {
    position: fixed;
    z-index: 2;
    width: 100%;
  }
</style>



